/* Yleinen layout */
/* Sivu saa kasvaa, vieritys sallittu */
html, body { height: auto; min-height: 100%; overflow-y: auto; }

/* Layout */
.demo-container { background: #eaf6eb; padding: 12px 16px 40px; }
.header { text-align: center; }
.logo { width: 72px; display: block; margin: 8px auto 0; }

.content {
  display: grid;
  grid-template-columns: 1.1fr 1fr 1.1fr;
  gap: 28px;
  align-items: start;
}

/* Vasen palsta – luettava teksti */
.left h3 { font-size: clamp(18px, 1.8vw, 22px); margin: 6px 0 8px; }
.left .benefits { font-size: clamp(15px, 1.25vw, 18px); line-height: 1.5; }
.left .benefits li { margin: 6px 0; }

/* Puhelinkehys */
.phone-frame {
  position: relative;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(0,0,0,.08);
  padding: 10px 10px 16px;
}
.phone-header { text-align: center; font-weight: 600; margin: 2px 0 6px; }

.profile-pic {
  display: block;
  width: 100%;
  aspect-ratio: 9/16;
  background: #f4f4f4;
  border-radius: 14px;
}

/* Status näkyy aina */
.status-chip {
  position: absolute;
  top: 8px; left: 10px;
  padding: 6px 10px;
  font-size: 12px;
  border-radius: 20px;
  color: #fff;
  box-shadow: 0 2px 10px rgba(0,0,0,.12);
}
.status-idle  { background: #9aa4a7; }
.status-live  { background: #2db36c; }
.status-meds  { background: #e35b51; }
.status-drink { background: #2c86da; }

.error-hint {
  text-align: center;
  font-size: 12px;
  color: #c44;
  margin-top: 6px;
}

/* Nappirivi */
.play-controls {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 12px;
}
.btn { border: 0; border-radius: 12px; padding: 8px 12px; color: #fff; cursor: pointer; }
.btn.green { background: #2db36c; }
.btn.red   { background: #e35b51; }
.btn.blue  { background: #2c86da; }
.btn.gray  { background: #6b7378; }
.btn.purple{ background: #7a57e6; }

/* Oikea kortti */
.panel-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0,0,0,.08);
  padding: 14px 16px;
}
.panel-title { font-weight: 700; margin-bottom: 8px; }
.panel-list  { margin: 0 0 10px 16px; }

/* Linkkimäinen nappi */
.linklike {
  background: transparent; color: #2c86da; border: 0; padding: 0;
  cursor: pointer; font-weight: 600;
}

/* Modal */
.modal {
  position: fixed; inset: 0; background: rgba(0,0,0,.3);
  display: grid; place-items: center; z-index: 50;
}
.modal-card {
  width: min(680px, 92vw);
  background: #fff; border-radius: 16px; padding: 12px 14px;
  box-shadow: 0 12px 32px rgba(0,0,0,.25);
}
.modal-header { display:flex; align-items:center; justify-content:space-between; }
.modal-header .close { border:0; background:#eee; border-radius:8px; padding:6px 10px; cursor:pointer; }

.modal-head{ display:flex; align-items:center; justify-content:space-between; margin-bottom:10px; }
.modal-title{ font-size:18px; font-weight:700; }
.modal-close{ border:none; background:transparent; font-size:18px; cursor:pointer; }

.modal-grid{ display:grid; gap:10px; margin-top:6px; }
.modal-row{ display:grid; grid-template-columns: 180px 1fr; gap:10px; align-items:center; }
.modal-row input{
  padding:10px 12px; border-radius:10px; border:1px solid #d9dee3; outline:none;
}
.modal-row span{ font-weight:600; }

.modal-actions{ display:flex; gap:10px; margin-top:14px; flex-wrap:wrap; }
/* Status-palkki videon päällä, aina näkyvissä */
.status-bar{
  position:absolute;
  top:8px;
  left:50%;
  transform:translateX(-50%);
  z-index:5;
  color:#fff;
  font-size:12px;
  font-weight:700;
  padding:4px 12px;
  border-radius:999px;
  box-shadow:0 2px 8px rgba(0,0,0,.15);
  background:rgba(0,0,0,.7); /* fallback */
}

/* värit tilan mukaan */
.status-bar.status-live  { background:#27ae60; }
.status-bar.status-meds  { background:#e74c3c; }
.status-bar.status-drink { background:#3498db; }

/* Varmistus että video ei peitä header/tilaa */
.phone-frame { position:relative; }
.profile-pic { display:block; position:relative; z-index:1; }
.phone-header { position:relative; z-index:2; margin-bottom:8px; }
/* --- Scroll-fix: anna sivun kasvaa, älä leikkaa mitään pois --- */
html, body {
  height: auto;
  min-height: 100%;
  overflow-y: auto;
}

/* Jos jossain oli 100vh / max-height / overflow:hidden → yliajetaan */
.demo-container,
.content,
.left {
  height: auto !important;
  max-height: none !important;
  overflow: visible !important;
}

/* Pidä sisältö ylhäällä, ei keskittämistä pystysuunnassa */
.content {
  align-items: start !important;
}

/* Luettavampi koko ja riviväli vasemmalle */
.left .benefits {
  font-size: clamp(15px, 1.25vw, 18px);
  line-height: 1.5;
}
.left h3 {
  font-size: clamp(18px, 1.6vw, 22px);
  margin-bottom: 8px;
}
.left .benefits li { margin: 6px 0; }
